/// app.js
import React from 'react';
import ReactMapGL, { Marker, Popup } from 'react-map-gl';
import { Container, Row, Col, Spinner, Card } from 'react-bootstrap';
import Pin from './alarmes-pin'
import CityInfo from './alarmes-info'
import ALARMES from './alarmes.json'
// Set your mapbox access token here
const MAPBOX_ACCESS_TOKEN = "pk.eyJ1Ijoib3Jpb2xnYWxjZXJhbiIsImEiOiJjanNyZmk3OHcxZGE4NDlvNDRqeXp1NjgwIn0.qHzCQHRw81Wtl9WawrA-kw";

// Data to be used by the LineLayer

export default class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      viewport: {
        longitude: 2.160302,
        latitude: 41.343862,
        zoom: 13,
        pitch: 0,
        bearing: -68
      },
      popupInfo: null
    };
  }
  _updateViewport = (viewport) => {
    this.setState({ viewport });
  }
  _renderCityMarker = (alarma, index) => {
    return (
      <Marker
        key={`marker-${index}`}
        longitude={alarma.coords[0]}
        latitude={alarma.coords[1]} >
        <Pin size={20} info={alarma} onClick={() => this.setState({ popupInfo: alarma })} />
      </Marker>
    );
  }

  _renderPopup() {
    const { popupInfo } = this.state;

    return popupInfo && (
      <Popup tipSize={5}
        anchor="top"
        longitude={popupInfo.coords[0]}
        latitude={popupInfo.coords[1]}
        closeOnClick={true}
        onClose={() => this.setState({ popupInfo: null })} >
        <CityInfo info={popupInfo} />
      </Popup>
    );
  }

  render() {
    const { viewport } = this.state;
    return (

      <ReactMapGL
        {...viewport}
        width="100%"
        height="100%"
        mapboxApiAccessToken={MAPBOX_ACCESS_TOKEN}
        mapStyle="mapbox://styles/oriolgalceran/cjvb5jas20cyv1fs1gjeo1vzf"
        onViewportChange={this._updateViewport}
      >
        {ALARMES.map(this._renderCityMarker)}
        {this._renderPopup()}


        <Container fluid={true}>
          <Row>
            <Col sm={3}>
              <br />

              <Card style={{ backgroundColor: 'rgba(240,255,255,0.90)' }}>
                <Card.Img src="./logo_port.svg" rounded="true" />

              </Card>
            </Col>
            <Col sm={9}>
            </Col>
          </Row>
          <Row>
            <Col sm={3}>
              <br />

              <Card style={{ backgroundColor: 'rgba(240,255,255,0.90)' }}>
                <Card.Body>
                  <Card.Title>Alerta</Card.Title>
                  <Card.Subtitle className="mb-2 text-muted">07:25 UTC</Card.Subtitle>
                  <Card.Text  className="text-left" >
                  <Spinner animation="grow" variant="danger" size="sm" />
                  &nbsp;  S'ha detectat un abocament a <strong>Moll de l'Energia 2</strong>
                  <hr/>
                  </Card.Text>
                  <Card.Link href="./">Veure càmera</Card.Link>
                  
                </Card.Body>

              </Card>
            </Col>
            <Col sm={9}>
            </Col>
          </Row>
        </Container>
      </ReactMapGL>
    );
  }
}
