import React, { PureComponent } from 'react';
import { Badge, Row, Col, Container } from 'react-bootstrap';
export default class CityInfo extends PureComponent {

    render() {
        const { info } = this.props;
        const displayName = `${info.nom}`;
        var badge =  <Badge variant="success">No es detecta presència de contaminants a l'aigua.</Badge>
        if (info.estat === "1") {
            badge =  <Badge variant="danger">S'han detectat contaminants a l'aigua</Badge>
        }
        return (
            <Container>
            <Row>
                <Col>
                    <h4><strong>{displayName}</strong> &nbsp;</h4>
                </Col>       </Row>
            <Row>
                <Col>
                {badge} <hr></hr>
                <a href="./">Veure càmera</a>
                </Col>
            </Row>
            </Container>
        );
    }
}